.home {
  max-width: 1500px;
  margin-left: auto;
  margin-right: auto;
}

.home__row {
  display: flex;
  z-index: 1;
  flex-wrap: wrap;
  margin-left: 5px;
  margin-right: 5px;
  /* justify-content: center; */
}

.home__image {
  width: 100%;
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
  z-index: -1;
  margin-bottom: -150px;
}

@media only screen and (max-width: 700px) {
  .home__image {
      margin-bottom: 0px;
  }
}